.big-calendar {
  background-color: #fff;
  padding-bottom: 50px;
  position: relative;

  .toolbar-container{
      position: absolute;
      bottom: calc(100% + 32px);
      display: flex;
      align-items: center;

      label {
        color: #323232;
        font-size: 14px;
        margin-left: 10px;
      }

      button {
        margin: 0
      }
  }
  .rbc-calendar {
    height: 900px;
    overflow: auto;

    .rbc-day-slot {
      overflow-x: hidden;
      overflow-y: auto;
    }
  }
  .rbc-events-container {
    .rbc-event {
      height: auto !important;
      max-width: 100%;
      width: auto !important;
    }
  }
  .rbc-event-label {
    display: none;
  }
}

.event-item-list {
  min-height: 300px;
  max-height: 600px;
  overflow-y: auto;
  padding: 20px;

  .event-item {
    display: flex;

    .status-highlight {
      border-radius: 6px !important;
      height: auto;
      margin-right: 5px;
      padding: 3px !important;
      width: auto;
    }
  }

  .event-item + .event-item {
    margin-top: 10px;
  }
}

.event-item {
  align-items: center;
  color: #727272;
  display: inline-flex;

  .priority .label-icon-container {
    display: flex;
    margin-top: 0;

    .style-icon-label  {
      display: none;
    }

    .style-icon {
      height: 12px;
      margin-right: 5px;
      width: 12px;
    }
  }
  .time {
    margin-right: 5px;
    white-space: nowrap;
  }

  .title {
    margin-right: 5px;
    white-space: nowrap;
  }
  .status-highlight {
    border-radius: 50% !important;
    height: 12px;
    margin-right: 5px;
    padding: 0 !important;
    width: 12px;
  }
}

.calendar-list {
  .todo-row {
    .row-time {
      white-space: nowrap;
    }
    .row-name {
      max-width: 200px;
    }
  }
}