.file-container {
  width: 100%;

  &.two-cols {
    display: inline-flex;
    flex: 0 0 45%;
    margin-top: 20px;
    width: 45%;

    &.hide-info {
      .file-info {
        display: none;
      }
      .file-type {
        img {
          border-radius: 15px;
          cursor: pointer;
          height: auto;
          width: 100%;
        }
      }
    }
  }

  > a {
    display: flex;
    overflow: hidden;
    text-decoration: none;
  }

  .file-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 16px;
    overflow: hidden;

    .filename {
      color: #323232;
      font-size: 13px;
      font-weight: 600;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    .filesize {
      color: #7c7c7c;
      font-size: 13px;
    }
  }
}
