.fileBox {
  align-content: center;
  flex-grow: 1;
  margin: 0 8px;
  min-height: 72px;

  .fileItem > button.icon-close {
    position: absolute;
    right: -23px;
    top: -20px;
  }

  .fileContainer {
    margin-bottom: 10px;

    .fileGrid {
      margin-top: 10px;
      padding: 0;

      .iconBox {
        background: #f5f5f5;
        border-bottom-left-radius: 12px;
        border-top-left-radius: 12px;
        padding: 8px;

        .fileIcon {
          width: 72px;
          height: auto;
        }
      }
      .fileItem {
        background: #f5f5f5;
        border-bottom-right-radius: 12px;
        border-top-right-radius: 12px;
        margin-left: -4px;
        padding: 8px;
        position: relative;

        .fileName {
          font-size: small;
          font-weight: bold;
          overflow-wrap: break-word;
          word-wrap: break-word;
          hyphens: auto;
        }
        .fileSize {
          font-size: x-small;
        }
      }
    }
  }
}
