.filter-container {
  .filter-header {
    margin-bottom: 20px;
    margin-top: 20px;

    p {
      color: #323232;
      font-size: 14px;
    }
  }

  .action-search-group {

    button {
      float: right;
    }

    .search-btn {
      margin-right: 0;
    }
    .cancel-btn {
      margin-left: 0;
    }
  }

  .filter-advance {
    margin-bottom: 8px;

    .filter-type {
      display: inline-block;

      .btn-day {
        margin-left: 0;
      }

      .btn-add {
        float: right;
        margin: 0 0 16px 8px;
      }
    }
    .filter-agenda {
      position: relative;

      .label-picker-btn {
        font-size: 1rem;
        margin: 0 16px 16px 0;
        padding: 6px 15px;
      }

      .date-range-picker {
        box-shadow: 2px 3px 5px rgba(0, 0,0, .5);
        position: absolute;
        z-index: 1;
      }

      .MuiFormControl-root {
        width: initial;
      }

      .rdrDefinedRangesWrapper {
        display: none;
      }
    }
  }
}

.filter-tool {
  display: flex;

  h3 {
    color: #323232;
    margin-right: 20px;
  }
  span.toggle-header {
    color: #3b90f9;
    cursor: pointer;
    position: relative;

    &:hover {
      color: #23527c;
    }

    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      border-bottom: 1px solid;
    }
  }
}