.complete-task-confetti {
  height: 330px;
  width: 100%;
  position: relative;
  overflow: hidden;
  z-index: 9;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &__close {
    position: absolute !important;
    z-index: 1;
    top: 8px;
    right: 8px;
  }

  .confetti {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height:  100%;
      pointer-events: none;
  }
}