.comment-input-container {
  .comment-input-toolbar {
    .title {
      flex-grow: 1;
    }
    .icon-close {
      margin-right: -14px;
      padding: 7px 14px;
    }
  }
  .title-red {
    color: red;
    font-size: 14px;
    margin-bottom: 5 px;
    margin-top: 5 px;
    height: 10 px;
  }

  .image-item-container {
    width: initial;

    .block-upload {
      margin: 0;
    }
  }

  .comment-input-content {
    overflow: auto;
    min-height: 250px;
  }

  .comment-input-actions {
    display: flex;
    justify-content: center;
    align-items: center;

    button + button {
      margin-left: 16px;
    }
  }
}
