$color-error: #f44336;

.required:after {
  content: ' *';
  color: $color-error;
}

.mt-40 {
  margin-top: 40px;
}
.mt-30 {
  margin-top: 30px;
}
.mt-20 {
  margin-top: 20px;
}
.mt-10 {
  margin-top: 10px;
}

.App {
  background: #edf0f5;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ddd;
  border-top: 0;

  .page-title {
    color: #323232;
    font-size: 20px;
    font-weight: 500;
  }
  
  a {
    cursor: pointer;
  }

  .v-align-top {
    color: #323232;
    font-size: 14px;
    font-weight: bold;
    line-height: 2.5;
    vertical-align: top;
  }
}

#component-error-text {
  color: $color-error;
}

.highlight {
  background-color: 'yellow';
  color: blue;
  font-weight: bold;
}

.hide {
  display: none;
}

.error-full-width {
  width: 100%;
}

.status-highlight {
  border-radius: 6px;
  color: #fff !important;
  padding: 0 7px;
  display: inline-block;

  &.completed {
    background-color: #00a93c;
  }
  &.in-completed {
    background-color: #559ef8;
  }
  &.cancel {
    background-color: #919191;
  }
}

.is-the-past {
  color: $color-error;
  font-weight: 500;

  .style-value .item-value {
    color: $color-error;
    font-weight: 500;
  }
}