.dialog-container {
  .MuiDialog-paper {
    max-height: calc(100% - 64px);
    overflow: hidden;
  }

  .dialog-toolbar {
    .title {
      flex-grow: 1;
    }
    .icon-close {
      margin-right: -14px;
      padding: 7px 14px;
    }
  }

  .dialog-content {
    max-height: calc(100vh - 185px);
    overflow: auto;
  }

  .dialog-actions {
    padding: 16px 24px;

    button + button {
      margin-left: 16px;
    }
  }
}
