.style-label-container {
  width: 100%;

  &.no-padding > .style-label {
    padding-left: 0;
    padding-right: 0;
  }

  > .style-label {
    color: #323232;
    font-size: 14px;
    font-weight: bold;
    padding-left: 15px;
    padding-right: 15px;
  }
  .style-value {
    .style-label {
      margin-left: 15px;
      margin-right: 15px;
      padding-left: 0;
      padding-right: 0;
    }
    .style-value {
      display: flex;
      flex-wrap: wrap;
      padding: 0 7px;

      > .file-container {
        margin-right: auto;
        padding-left: 7px;
        padding-right: 7px;
      }
    }
  }
  img {
    height: 46px;
    width: 46px;
  }

  .mt-20 {
    margin-top: 20px;
  }
}
