.content {

  .page-detail {
    flex-grow: 1;

    .status-highlight {
      height: 25px;
      line-height: 25px;
      border-radius: 6px;
    }
  }

  .title-container {
    padding-bottom: 20px;
  }

  .attached-files {
    border: 1px solid #edf0f5;
    padding: 15px 0 33px;

    p.style-label {
      border-bottom: 1px solid #edf0f5;
    }
  }

  .item-value {
    color: #727272;
    font-size: 14px;
    margin-top: 10px;

    &.error {
      color: red;
    }

    .deal {
      > * {
        font-size: 14px;
      }

      a.highlight-id {
        color: #3b90f9;
        text-decoration: blink;
      }
    }
  }
}
