.marginBlock {
  margin-top: 10px;
  margin-left: 13px;
}
.styleValue {
  color: #323232;
  font-size: 13px;
}

.comment-category-content {
  display: flex;

  > svg {
    margin-right: 15px;
  }
  .title {
    font-size: 14px;
    font-weight: bold;

    &.primary {
      color: #00a93c;
    }
    &.secondary {
      color: #a9a9a9;
    }
  }
  .icon {
    &.primary {
      color: #00a93c;
    }
    &.secondary {
      color: #a9a9a9;
    }
  }
  .comment-result {
    font-weight: bold;
    @extend .styleValue;

    @extend .marginBlock;
  }
  .files-container {
    display: flex;

    .file-container {
      padding-left: 13px;
      padding-top: 9px;
    }

    .comment-file {
      display: inline-block;
      @extend .marginBlock;
    }
    img {
      height: auto;
      width: 166px;
    }
  }
  .comment-content {
    margin-left: 13px;
    @extend .styleValue;
  }
  .comment-note {
    margin-left: 13px;
    margin-top: 20px;

    .comment-note-label {
      font-weight: bold;
      @extend .styleValue;
    }
    .comment-note-value {
      @extend .styleValue;
    }
  }
}

.comment-data-container {
  border: 1px solid;
  margin-top: 40px;
  margin-bottom: 18px;
  padding: 10px;

  &.status-2 {
    border-color: #00a93c;
  }
  &.status-3 {
    border-color: #a9a9a9;
  }
}

.comment-items {
  .comment-item {
    .image-list li {
      width: auto !important;
    }
  }
}
