.iloading {
  display: none;
  position: fixed;
  top: 50%;
  width: 100%;
  text-align: center;
  z-index: 10000;

  &::before {
    background-color: rgba(0, 188, 212, 0);
    bottom: 0;
    content: '';
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    transition: background-color 0.5s;
  }

  &.active {
    display: block;
  }
}
